.btn {
  line-height: 24px;
  display: inline-block;
  vertical-align: top;
  padding-left: 4px;
  padding-right: 4px;
  color: inherit;
  font-family: inherit;
}

.btn--git {
  width: 50%;
  text-align: right;
  color: #91B859;
}

.btn--git:hover {
  opacity: 0.9;
}

.btn--git:active {
  opacity: 0.6;
}

.btn--external {
  color: #FFCB6B;
  width: 50%;
}

.btn--external:hover {
  opacity: 0.9;
}

.btn--external:active {
  opacity: 0.6;
}

.btn--material {
  border-radius: 2px;
  box-shadow: 0 1px 2px 0 #000;
  border-style: none;
  padding-top: 5px;
  padding-bottom: 5px;
  text-transform: uppercase;
  width: 100%;
}

.btn--material::active {
  box-shadow: 0 0 8px 0 #000;
  outline: none;
}

.btn--material:focus {
  outline: none;
  background-color: #689F38;
}

.btn--green {
  background-color: #91B859;
}

.page-header {
  box-shadow: 0 0 16px #000;
  background-color: #37474F;
  position: relative;
}

.page-header__container {
  max-width: 280px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 15px;
}

.menu {
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  display: none;
}

.menu--open {
  display: block;
}

.menu__list {
  display: flex;
  flex-direction: column;
}

.menu__item {
  position: relative;
  text-align: center;
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: #37474F;
  border-top: 1px solid #37474F;
  border-bottom: 1px solid #000;
}

.menu__item:last-child {
  padding-right: 0;
}

.menu__link {
  color: inherit;
  font-size: 20px;
  position: relative;
}

.menu__link:active {
  opacity: 0.6;
}

.about__container {
  padding-top: 50px;
  padding-bottom: 30px;
  max-width: 280px;
  margin: 0 auto;
  border-bottom: 1px dashed;
}

.about__heading {
  color: #39ADB5;
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 20px;
  display: block;
}

.about__resume {
  border-bottom: 1px dotted;
  color: inherit;
  color: #91B859;
}

.social {
  flex-grow: 1;
  box-shadow: 0 2px 1px -2px #000;
  position: relative;
  z-index: 10;
}

.social__container {
  max-width: 280px;
  margin: 0 auto;
  padding-top: 60px;
}

.social__list {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.social__item {
  display: flex;
  align-items: center;
  height: 55px;
  padding-left: 8px;
  padding-right: 8px;
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: 20px;
  border-radius: 2px;
  background-color: #37474F;
  box-shadow: 0 0 2px 0 #000;
}

.social__item:active {
  box-shadow: 0 1px 6px 0 #000;
  opacity: 0.9;
}

.social__link {
  display: flex;
  align-items: center;
}

.projects {
  flex-grow: 1;
  box-shadow: 0 0 2px 0 #000;
  position: relative;
  z-index: 10;
}

.projects__container {
  padding-top: 20px;
  max-width: 280px;
  margin: 0 auto;
}

.projects__heading {
  max-width: 280px;
  margin: 0 auto;
  padding-bottom: 30px;
  position: relative;
}

.projects__heading::after {
  position: absolute;
  display: block;
  content: '';
  width: 100%;
  bottom: 30px;
  left: 0;
  border-bottom: 1px dashed;
}

.projects__items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.projects__item {
  width: 100%;
  background-color: #37474F;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 1px 2px #000;
  border-radius: 2px;
  position: relative;
  overflow: hidden;
}

.projects__item:active {
  box-shadow: 0 1px 8px #000;
}

.projects__image {
  display: block;
  width: 100%;
  height: 200px;
}

.projects__name {
  color: #E53935;
  line-height: 21px;
  padding-top: 12px;
  padding-bottom: 8px;
  padding-left: 8px;
  padding-right: 8px;
}

.projects__description {
  font-size: 14px;
  line-height: 22px;
  padding-left: 8px;
  padding-right: 8px;
  padding-bottom: 8px;
  flex-grow: 1;
}

.projects__buttons {
  padding: 4px;
  display: flex;
  align-items: center;
}

.skills {
  flex-grow: 1;
  box-shadow: 0 0 2px 0 #000;
  position: relative;
  z-index: 10;
}

.skills__container {
  max-width: 280px;
  margin: 0 auto;
  padding-top: 50px;
  padding: 20px;
}

.skills__heading {
  position: relative;
  padding-bottom: 30px;
}

.skills__heading::after {
  position: absolute;
  display: block;
  content: '';
  width: 100%;
  bottom: 30px;
  left: 0;
  border-bottom: 1px dashed;
}

.skills__wrapper {
  display: flex;
  overflow: hidden;
  justify-content: space-between;
  flex-wrap: wrap;
}

.skills__category {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  width: 100%;
}

.skills__category::before {
  position: absolute;
  content: '';
  display: block;
  width: 5px;
  top: 40px;
  left: 8px;
  height: 105%;
  background-color: #E53935;
}

.skills__category:last-child {
  margin-bottom: 0;
}

.skills__list {
  padding-left: 40px;
}

.skills__item {
  padding: 0;
  margin: 0;
  align-items: center;
  display: flex;
  position: relative;
  padding-left: 8px;
  padding-right: 8px;
  padding-bottom: 12px;
  padding-top: 12px;
  margin-right: auto;
  margin-bottom: 12px;
  box-shadow: 0 1px 2px #000;
  background-color: #37474F;
  border-left: 5px solid #E53935;
}

.skills__item:last-child {
  margin-bottom: 2px;
}

.skills__item::before {
  display: block;
  content: '';
  position: absolute;
  top: 50%;
  left: -45px;
  margin-top: -10px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 5px solid #E53935;
  background-color: #455A64;
}

.skills__item::after {
  display: block;
  position: absolute;
  content: '';
  top: 50%;
  left: -20px;
  margin-top: -8px;
  border-right: 15px solid #E53935;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
}

.skills__item:active {
  box-shadow: 0 1px 8px #000;
}

.skills__rating {
  bottom: 3px;
}

.skills__name {
  padding-left: 8px;
}

.skills__title {
  margin-bottom: 10px;
  padding-left: 40px;
}

.skills__item:active .rating__item {
  box-shadow: inset 0 1px 4px #000;
}

.rating {
  width: 95%;
  height: 5px;
  position: absolute;
  justify-content: center;
  display: flex;
  left: 50%;
  transform: translateX(-50%);
  overflow: hidden;
  background-image: linear-gradient(to right, #E53935 0%, #FFCB6B 50%, #91B859 100%);
}

.rating__item {
  width: 20%;
  display: block;
  height: 5px;
  background-color: transparent;
  border-left: 4px solid #37474F;
  border-right: 4px solid #37474F;
  box-shadow: inset 0 1px 2px #000, 0 0 0 2px #37474F;
  outline: 1px solid #37474F;
}

.rating[data-star-value="4"] > .rating__item:nth-child(n+5) {
  background-color: #455A64;
}

.rating[data-star-value="3"] > .rating__item:nth-child(n+4) {
  background-color: #455A64;
}

.rating[data-star-value="2"] > .rating__item:nth-child(n+3) {
  background-color: #455A64;
}

.rating[data-star-value="1"] > .rating__item:nth-child(n+2) {
  background-color: #455A64;
}

