  padding-bottom: 8px;
  padding-right: 16px;
  padding-left: 16px;
  margin-bottom: 18px;
  border-bottom: 1px solid #212121;
}

.contact__number {
  display: flex;
  align-items: center;
  padding-left: 16px;
  padding-right: 16px;
  padding-bottom: 16px;
}

.contact__text {
  padding-left: 8px;
  user-select: none;
}

.form {
  background-color: #37474F;
  box-shadow: 0 1px 2px 0 #000;
  display: flex;
  width: 100%;
  flex-direction: column;
  border-radius: 2px;
}

.form__group {
  padding-bottom: 16px;
  margin-bottom: 12px;
  position: relative;
  padding-left: 16px;
  padding-right: 16px;
}

.form__group:last-child {
  text-align: center;
  margin-bottom: 0;
}

.form__input {
  width: 100%;
  background-color: #37474F;
  border-style: none;
  border-bottom: 1px solid #455A64;
  color: inherit;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-right: 8px;
}

.form__input:focus {
  outline: none;
}

.form__label-box {
  position: relative;
}

.form__label-box::before {
  position: absolute;
  content: '';
  height: 2px;
  width: 0;
  bottom: 1px;
  background-color: #039BE5;
  transition-property: width;
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  left: 50%;
}

.form__label-box::after {
  position: absolute;
  content: '';
  height: 2px;
  width: 0;
  bottom: 1px;
  right: 50%;
  background-color: #039BE5;
  transition-property: width;
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.form__label-box--area::after {
  bottom: 9px;
}

.form__label-box--area::before {
  bottom: 9px;
}

.form__label {
  position: absolute;
  top: -32.5px;
  transition: font-size 0.2s cubic-bezier(0.4, 0, 0.2, 1), transform 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  pointer-events: none;
}

.form__label--area {
  top: -106px;
}

.form__message {
  width: 100%;
  min-height: 100px;
  background-color: #37474F;
  color: inherit;
  border-style: none;
  resize: none;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-right: 8px;
  border-bottom: 1px solid #455A64;
}

.form__message:focus {
  outline: none;
}

.form__message:focus ~ .form__label-box::before,
.form__message:focus ~ .form__label-box::after,
.form__input:focus ~ .form__label-box::before,
.form__input:focus ~ .form__label-box::after {
  width: 50%;
}

.form__message:focus ~ .form__label-box label,
.form__message:valid ~ .form__label-box label,
.form__input:focus ~ .form__label-box label,
.form__input:valid ~ .form__label-box label {
  font-size: 14px;
  transform: translateY(-25px);
}

label.error {
  font-size: 10px;
  position: absolute;
  font-weight: 500;
  text-rendering: optimizeLegibility;
  width: 100%;
  left: 16px;
  bottom: -8px;
}

.page-footer {
  position: relative;
  bottom: 0;
  width: 100%;
  font-size: 10px;
  background-color: #37474F;
}

.page-footer__container {
  max-width: 280px;
  margin: 0 auto;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
}

.page-footer__copyright {
  flex-grow: 1;
  text-align: left;
}

.page-footer__link {
  color: #91B859;
  border-bottom: 1px dotted;
}

.page-footer__link:hover {
  opacity: 0.9;
}

.page-footer__link:active {
  opacity: 0.6;
}

